.rmdb-movieinfo {
  background-size: cover !important;
  background-position: center !important;
  width: 100%;
  height: 600px;
  padding: 40px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-animation: animateMovieinfo 1s;
          animation: animateMovieinfo 1s;
}

@-webkit-keyframes animateMovieinfo {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@keyframes animateMovieinfo {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

.rmdb-movieinfo-content {
  /*max-width: 1280px;*/
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: rgb(0, 0, 0, 0.7);
  position: relative;
}

.rmdb-flexcontainer {
  display: -ms-flexbox;
  display: flex;
}

.rmdb-genres {
  margin-right: 100px;
}

.rmdb-movieinfo-info {
  display: -ms-flexbox;
  display: flex;
}

.rmdb-movieinfo h4 {
  font-size: 'Abel',sans-serif;
}

.rmdb-movieinfo-info li {
  list-style: none;
  border-right: 1px solid #fff;
  margin-right: 10px;
  padding-right: 10px;
  font-family: 'Abel',sans-serif;
}

.rmdb-movieinfo-info li:last-child {
  border-right: none;
  margin-right: 0;
}

.rmdb-movieinfo-thumb {
  width: 350px;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 0px;
}

.rmdb-movieinfo-text {
  font-family: 'Abel', sans-serif;
  height: 100%;
  width: auto;
  padding: 40px;
  color: #fff;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  left: 360px;
}


.movie_title {
  font-family: 'Abel', sans-serif;
  font-size:48px;
  margin: 0;
}

.rmdb-movieinfo-text h3 {
  font-size: 16px;
  line-height: 0;
  margin-top: 30px;
}

.rmdb-movieinfo-text p {
  font-family: 'Abel', sans-serif;
  font-size: 18px;
  line-height: 26px;
}



.rmdb-rating {
  width: 250px;
  height: 20px;
  margin-top: 20px;
  position: relative;
}

.rmdb-score {
  position: absolute;
  margin: 0;
  right: 0px;
  top: -3px;
}

.rmdb-director {
  margin: 0;
}

.fa-film {
  position: absolute;
  bottom: 40px;
  right: 40px;
  color: #fff;
}

meter::-webkit-meter-bar {
  background: #FFF;
  width:200px;
}
meter::-webkit-meter-optimum-value {
  background: -webkit-gradient(linear, left top, left bottom, from(#16d47b));
  background: -webkit-linear-gradient(top, #16d47b);
  background: linear-gradient(to bottom, #16d47b);
}
meter::-webkit-meter-suboptimum-value {
  background: -webkit-gradient(linear, left top, left bottom, from(#fbb450));
  background: -webkit-linear-gradient(top, #fbb450);
  background: linear-gradient(to bottom, #fbb450);
}
meter::-webkit-meter-even-less-good-value {
  background: -webkit-gradient(linear, left top, left bottom, from(#ee5f5b));
  background: -webkit-linear-gradient(top, #ee5f5b);
  background: linear-gradient(to bottom, #ee5f5b);
}


@media only screen and (max-width: 768px) {
  .rmdb-movieinfo {
    width: 100vw;
    padding: 20px 10px;
  }
  .rmdb-movieinfo-thumb {
    display: none;
  }
  .rmdb-movieinfo-text {
    position: absolute;
    left: 0;
    width: 100%;
    overflow: hidden;
    padding: 20px;
  }
  .rmdb-flexcontainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .movie_title {
    font-size: 24px;
  }
  .rmdb-movieinfo-text p {
    font-size: 14px;
    line-height: 1.1;
  }
  .rmdb-movieinfo h4 {
    margin: 5px 0;
  }
  .rmdb-movieinfo-info li {
    border-right: none;
    margin-right: 5px;
    padding-right: 5px;
    font-family: 'Abel',sans-serif;
  }

  .rmdb-movieinfo-info li:last-child {
    border-right: none;
    margin-right: 0;
  }
  .fa-film {
    display: none;
  }
}


